'use client'

import styled from './MakeFormula.module.scss';
import { MakeFormulaProps } from './types';



export default function MakeFormula({lines, circles}: MakeFormulaProps) {

     const getLength = (id: string) => {
        const line = lines.find((item) => item.id === id)
        if(line){
            const lengthPx = Math.sqrt(
                Math.pow(line.point2.x - line.point1.x, 2) + 
                Math.pow(line.point2.y - line.point1.y, 2)
              );
              return lengthPx.toFixed(1)
        }
     }
  return (
      <div className={styled.menuRight}>
        <div className={styled.menuRight_title}>Посчитать значения</div>
        {lines.map((item) => (
            <div className={styled.menuRight_item}>
                <div className={styled.menuRight_item_title}>Линия: <span>{item.id}</span></div>
                <div className={styled.menuRight_item_title}>Длина: <span>{getLength(item.id)}px</span></div>
            </div>
        ))}
      </div>
  );
}
